          i[2].style.fill = "#C4C4C4";
          i[3].style.fill = "#C4C4C4";
          i[4].style.fill = "#C4C4C4";
          i[0].style.fill = "#C4C4C4";
          i[6].style.fill = "#C4C4C4";
        } else if (k == 6) {
          i[k].style.fill = "rgb(254, 196, 109)";
