<template>
  <div class="header__container" ref="container__ref">
    <div class="header__layout">
      <div class="header__top">
        <div class="header__left">
          <p ref="p__ref">
            Total Nfts : <span>{{ totalNfts }}Lakhs</span>
          </p>
          <p>
            Market Cap : <span>{{ marketCap }} ETH</span>
          </p>
          <p>
            Dominance : <span>Crypto Punk:{{ dominance }} ETH</span>
          </p>
        </div>
        <div class="header__right">
          <div @click="showLang" class="dropdown">
            <label class="dropbtn">{{ lang }}</label>
            <img src="../assets/menu-down.png" class="menu-down" />
            <ul v-if="show">
              <li @click="langSelect1()" ref="lang1__ref">English</li>
              <li @click="langSelect2()" ref="lang2__ref">Tamil</li>
            </ul>
          </div>
          <div class="mode__select">
            <label @click="selectMode()" ref="mode__ref" class="darkMode"
              >Dark Mode</label
            >
            <img v-if="mode == 'dark'" src="../assets/dark-mode.png" />
            <img
              v-if="mode == 'light'"
              src="../assets/sun.png"
              width="18"
              height="15"
            />
          </div>
        </div>
      </div>
      <div class="header__bottom">
        <div class="header__bottom__left">
          <img src="../assets/nft-logo.png" class="nft-logo" />
          <router-link to="/" class="link">Home</router-link>
          <router-link to="/" class="link">Upcoming</router-link>
          <router-link to="/" class="link">New</router-link>
          <router-link to="/" class="link">Contact Us</router-link>
          <router-link to="/" class="link">About Us</router-link>
        </div>
        <div class="header__bottom__right">
          <span class="p-input-icon-left">
            <i class="pi pi-search" />
            <InputText
              class="search"
              type="text"
              v-model="value1"
              placeholder="Search"
            />
          </span>
          <button class="list">List your NFT</button>
          <button class="login">Login</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import InputText from "primevue/inputtext";

export default {
  name: "header",
  data() {
    return {
      lang: "English",
      show: false,
      totalNfts: this.$store.state.totalNfts,
      marketCap: this.$store.state.marketCap,
      dominance: this.$store.state.dominance,
      mode: this.$store.state.selectMode,
    };
  },
  components: { InputText },
  methods: {
    showLang() {
      if (this.show == false) {
        this.show = true;
      } else {
        this.show = false;
      }
    },
    langSelect1() {
      const $ref__english = this.$refs.lang1__ref;
      this.lang = $ref__english.innerText;
    },
    langSelect2() {
      const $ref__tamil = this.$refs.lang2__ref;
      this.lang = $ref__tamil.innerText;
    },
    selectMode() {
      const $ref__mode = this.$refs.mode__ref;
      const $ref_container = this.$refs.container__ref;

      if (this.mode == "dark") {
        this.mode = "light";
        this.$store.state.selectMode = "light";
        $ref__mode.innerText = "Day Mode";
        $ref_container.classList.add("light");
        console.log("from header", this.$store.state.selectMode);
      } else {
        this.mode = "dark";
        this.$store.state.selectMode = "dark";
        $ref__mode.innerText = "Dark Mode";
        $ref_container.classList.remove("light");
        console.log("from header", this.$store.state.selectMode);
      }
    },
  },
};
</script>

