                src="../assets/image_1.png"
                width="40"
                height="40"
                style="margin-right: 15px; margin-left: 15px"
              />
              <span class="image-text">{{ data.name }}</span>
            </template>
          </Column>
          <Column field="seven_day_volume" header="Volume (7d)">
            <template #body="{ data }">
              <span class="text" style="margin-left: 15px">{{
