            </template>
          </Column>
          <Column field="sales_all_time_high" header="Sales (All time)">
            <template #body="{ data }">
              <span class="text" style="margin-left: 15px">{{
                data.sales_all_time_high
              }}</span>
            </template>
          </Column>
          <Column field="seven_day_average_price" header="Avg Price (7d)">
            <template #body="{ data }">
