            <template #body="{ data }">
              <span class="text" style="margin-left: 15px">{{
                data.num_owners
              }}</span>
            </template>
          </Column>
          <Column field="market_cap" header="Market Cap">
            <template #body="{ data }">
              <span
                class="text"
                style="margin-left: 15px; margin-right: 10px"
                >{{ data.market_cap }}</span
              >
