<script>
import DataTable from "primevue/datatable";
import Column from "primevue/column";
import axios from "axios";
import { mapState } from "vuex";

export default {
  name: "all_collections",
  data() {
    return {
      // all_collections: this.$store.state.collections,
      opensea_collections: this.$store.state.opensea_collections,
    };
  },
  components: {
    DataTable,
    Column,
  },
  computed: {
    ...mapState(["selectMode"]),
  },
  mounted() {

    const url = '/collections';

    axios.get(url).then((res) => {
      let collections = res.data;
      for (let collection of collections) {
        this.opensea_collections.push({
          id: collection.id,
          name: collection.name,
          image: collection.image,
          seven_day_volume: collection.seven_day_volume,
          volume_all_time_high: collection.volume_all_time_high,
          sales_all_time_high: collection.sales_all_time_high,
          seven_day_sales: collection.seven_day_sales,
          seven_day_average_price: collection.seven_day_average_price,
          total_supply: collection.total_supply,
          num_owners: collection.num_owners,
          market_cap: collection.market_cap,
        });
      }
    }).catch((error) => {
      console.log(error)
    })
    // axios
    //   .get("https://api.opensea.io/api/v1/collections?offset=0&limit=300")
    //   .then((res) => {
    //     let collections = res.data.collections;
    //     let i = 0;
    //     for (let collection of collections) {
    //       this.opensea_collections.push({
    //         id: ++i,
    //         name: collection.name,
    //         image: collection.image_url,
    //         seven_day_volume: collection.stats.seven_day_volume,
    //         volume_all_time_high: collection.stats.one_day_volume,
    //         sales_all_time_high: collection.stats.one_day_sales,
    //         seven_day_sales: collection.stats.seven_day_sales,
    //         seven_day_average_price: collection.stats.seven_day_average_price,
    //         total_supply: collection.stats.total_supply,
    //         num_owners: collection.stats.num_owners,
    //         market_cap: collection.stats.market_cap,
    //       });
    //     }
    //   })
    //   .catch((error) => {
    //     console.log(error);
    //   });

    console.log("api_data:", this.opensea_collections);
  },
};
</script>

