import { createApp } from "vue";
import App from "./App.vue";
import router from "./router";
import store from "./store";

import PrimeVue from "primevue/config";
import "primevue/resources/primevue.min.css";
import "primeicons/primeicons.css";
import "primeflex/primeflex.css";

import Header from "./components/header.vue";
import Carousel from "./components/carousel.vue";
import RareCollections from "./components/rare_collections.vue";
import AllCollections from "./components/all_collections.vue";
import RarityRank from "./components/RarityRank.vue";
import FilterRarity from "./components/FilterRarity.vue";
import SubscribeCard from "./components/subscribe_card.vue";
import Footer from "./components/footer.vue";

const app = createApp(App);
// app.component("DataTable", DataTable);
app.component("Header", Header);
app.component("Carousel", Carousel);
app.component("RareCollections", RareCollections);
app.component("AllCollections", AllCollections);
app.component("RarityRank", RarityRank);
app.component("FilterRarity", FilterRarity);
app.component("SubscribeCard", SubscribeCard);
app.component("Footer", Footer);
app.use(store).use(router).use(PrimeVue).mount("#app");
